@import-normalize; /* bring in normalize.css styles */

/* rest of app styles */

html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}


html {
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue,
    PingFang SC, Hiragino Sans GB, STHeiti, Microsoft Yahei, Wenquanyi Micro Hei, sans-serif;
  font-size: 75%;
}
@media only screen and (max-width: 320px) {
  html {
    font-size: 60%;
  }
};
@media only screen and (max-width: 375px)  {
  html {
    font-size: 70%;
  }
};

  
/* 防止移动端chrome的pull-to-refresh effect  */
body {
  position: fixed;
  overflow: hidden;
  margin: 0;
  padding: 0;
}

/* body.noscroll{
  position:fixed;
  overflow:hidden;
} */


/* Reset `button` and button-style `input` default styles */
input[type="submit"],
input[type="reset"],
input[type="button"],
button {
  outline:none;
    background: none;
    border: 0;
    color: inherit;
    /* cursor: default; */
    font: inherit;
    line-height: normal;
    overflow: visible;
    padding: 0;
    -webkit-appearance: button; /* for input */
    -webkit-user-select: none; /* for button */
       -moz-user-select: none;
        -ms-user-select: none;
}
input::-moz-focus-inner,
button::-moz-focus-inner {
    border: 0;
    padding: 0;
}


/* custom */
a {
  color: #7e8c8d;
  text-decoration: none;
  -webkit-backface-visibility: hidden;
}

li {
  list-style: none;
}