.nav-close {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 14rem;
  background: linear-gradient(transparent 0, rgba(0, 0, 0, 0.8) 30%, #000);
  display: flex;
  justify-content: center;
  align-items: center;

  button {
    width: 4rem;
    height: 4rem;
    background: url(data:image/png;base64,)
      50% / contain no-repeat;
  }
}

@media only screen and (max-width: 425px) {
  .nav-container {
    width: 100%;
    .nav-list {
      padding: 3rem 4rem 15rem;
    }
  }
}
